import { useRef, useState } from "react";
import styled from "styled-components";
import {
  GOOGLE_SHEETS_API,
  RECAPTCHA_PUBLIC_KEY,
} from "../../constants/appConstants";
import ArrowSVG from "../../assets/arrow-icon.svg";
import ReCAPTCHA from "react-google-recaptcha";
import { Formik, Field, Form, useField } from "formik";
import * as Yup from "yup";
import { LoadingSpinner } from "../Loading";

interface FormData {
  choice: string;
  handle: string;
  address: string;
  email: string;
}

interface Props {
  handleOpenModal: any;
}

const MyTextInput = ({ label, ...props }: any) => {
  // useField() returns [formik.getFieldProps(), formik.getFieldMeta()]
  // which we can spread on <input>. We can use field meta to show an error
  // message if the field is invalid and it has been touched (i.e. visited)
  const [field, meta] = useField(props);
  return (
    <div
      className={
        label !== "" ? "text-input-container" : "small-input-container"
      }
    >
      <label htmlFor={props.id || props.name}>{label}</label>
      <input className="text-input" autocomplete="off" {...field} {...props} />
      {meta.touched && meta.error ? (
        <div className="error">{meta.error}</div>
      ) : null}
    </div>
  );
};

export const FormSection: React.FC<Props> = ({ handleOpenModal }: Props) => {
  const recaptchaRef = useRef<ReCAPTCHA>();
  const [loading, setLoading] = useState<boolean>(false);
  const [remindCaptcha, setRemindCaptcha] = useState<boolean>(false);

  const handleSubmitCheck = () => {
    // @ts-ignore
    if (!recaptchaRef.current.getValue()) {
      setRemindCaptcha(true);
    } else {
      setRemindCaptcha(false);
    }
  };

  const handleSubmission = async (values: FormData) => {
    let formData = new FormData();

    formData.append(
      "We will need to use either you Twitter or Discord to identify you in the case that you win - please select which one you prefer using:",
      values.choice
    );
    formData.append(
      "Please enter you twitter or discord handle.",
      values.handle
    );
    formData.append(
      "Please enter your Solana public address that you will be using to compete:",
      values.address
    );
    formData.append(
      "Please enter an email that we can use to contact you",
      values.email
    );

    await fetch(GOOGLE_SHEETS_API, {
      method: "POST",
      body: formData,
    })
      .then((response) => {
        console.log(response, "response");
        setLoading(false);
        handleOpenModal(true);
      })
      .catch((error) => console.error("Error!", error.message));
  };

  return (
    <FormContainer>
      <FormContent>
        <HeaderText>Sign Up</HeaderText>
        <Formik
          initialValues={{
            choice: "",
            handle: "",
            address: "",
            email: "",
          }}
          validationSchema={Yup.object({
            choice: Yup.string()
              .oneOf(["Twitter", "Discord"], "Invalid Choice")
              .required("Required"),
            handle: Yup.string().required("Required"),
            address: Yup.string()
              .min(32, "Must be at minimum 32 characters")
              .max(44, "Must be at maximum 44 characters")
              .required("Required"),
            email: Yup.string()
              .email("Invalid email address")
              .required("Required"),
          })}
          onSubmit={(values) => {
            // @ts-ignore
            if (!recaptchaRef.current.getValue()) {
              return;
            } else {
              setLoading(true);
              handleSubmission(values);
            }
            console.log("Form submitted");
          }}
        >
          {({ values }) => (
            <Form className="form">
              <label className="platform-header-label">
                Where do you prefer us to get in touch?
              </label>
              <div className="platform-radio-group" role="group">
                <label style={{ display: "flex", flexDirection: "row" }}>
                  <Field
                    className="radio"
                    type="radio"
                    name="choice"
                    value="Twitter"
                  />
                  Twitter
                </label>
                <label style={{ display: "flex", flexDirection: "row" }}>
                  <Field
                    className="radio"
                    type="radio"
                    name="choice"
                    value="Discord"
                  />
                  Discord
                </label>
              </div>

              <MyTextInput
                label=""
                name="handle"
                type="text"
                placeholder={
                  values.choice !== ""
                    ? values.choice + " Handle"
                    : "Please Select A Platform Above"
                }
              />
              <MyTextInput
                label="Solana Public Address"
                name="address"
                type="text"
                placeholder="kxhe...39d"
              />
              <MyTextInput
                label="Email Address"
                name="email"
                type="text"
                placeholder="example@gmail.com"
              />
              <ReCAPTCHAContainer>
                <ReCAPTCHA
                  sitekey={RECAPTCHA_PUBLIC_KEY}
                  // @ts-ignore
                  ref={recaptchaRef}
                  type="image"
                  onChange={() => {
                    setRemindCaptcha(false);
                  }}
                />
                {remindCaptcha && (
                  <WarningText>Please complete the reCAPTCHA.</WarningText>
                )}
              </ReCAPTCHAContainer>
              <SubmitButton type="submit" onClick={() => handleSubmitCheck()}>
                {loading ? (
                  <LoadingSpinner />
                ) : (
                  <>
                    Sign Up <ArrowImg src={ArrowSVG} />
                  </>
                )}
              </SubmitButton>
            </Form>
          )}
        </Formik>
      </FormContent>
    </FormContainer>
  );
};

const FormContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
  @media (min-width: 1200px) {
    align-items: flex-end;
  }
`;

const FormContent = styled.div`
  @media (min-width: 700px) {
    background: #464370;
    border-radius: 15px;
    padding: 2rem 3rem;
  }
`;

const SubmitButton = styled.button`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  margin: 1rem 0;
  width: 13rem;
  text-align: center;
  text-decoration: none;
  border-radius: 1.5rem;
  color: #2e2350;
  padding: 0.3rem 2.8rem;
  font-style: normal;
  font-weight: bold;
  font-size: 1rem;
  line-height: 40px;
  cursor: pointer;
  border: none;

  background: linear-gradient(94.82deg, #b39bff 9.15%, #d6cbf8 115.01%);
  box-shadow: 4px 4px 30px rgba(255, 255, 255, 0.1);
  border-radius: 10px;

  transition: transform 250ms ease-out;
  &:hover {
    transform: scale(1.05);
  }
`;

const HeaderText = styled.h1`
  text-align: center;
  width: 100%;
  @media (min-width: 450px) {
    margin-bottom: 2rem;
  }

  @media (min-width: 1200px) {
    margin-bottom: 2.5rem;
  }
`;

const WarningText = styled.div`
  color: #ff5f5f;
  font-weight: 400;
  font-size: 14px;
`;

const ReCAPTCHAContainer = styled.div`
  height: 90px;
  @media (min-width: 1200px) {
    margin-top: 1.5rem;
    margin-bottom: 2.5rem;
  }
`;

const ArrowImg = styled.img`
  height: 10px;
  width: 15px;
  margin-left: 5px;
`;
