import { useState } from "react";
import styled from "styled-components";
import { TopNavBar } from "../landing/TopNavBar";
import { FormSection } from "./FormSection";
import { CompInfoSection } from "./CompInfoSection";
import { SignUpModal } from "./SignUpModal";
import { Banner } from "./Banner";
import "./index.css";

export const CompetitionContainer = () => {
  const [openModal, setOpenModal] = useState<boolean>(false);
  const handleOpenModal = (bool: boolean) => {
    setOpenModal(bool);
  };
  return (
    <PaneBody>
      <Banner />
      {openModal && <SignUpModal />}
      <PaneSection>
        <TopNavBar />
      </PaneSection>
      <CompetitionSection>
        <CompInfoSection />
        <FormSection handleOpenModal={handleOpenModal} />
      </CompetitionSection>
    </PaneBody>
  );
};

const PaneBody = styled.body`
  font-family: var(--ff-accent);
  font-weight: 200;
  color: var(--clr-neutral-100);
  background: #180f34;
  min-height: 100vh;
  position: relative;
  width: 100%;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const PaneSection = styled.section`
  margin: auto;
  width: min(90%, 70.5rem);
`;

const CompetitionSection = styled(PaneSection)`
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  @media (min-width: 1200px) {
    flex-direction: row;
    align-items: baseline;
  }
`;
